import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";
import {DatePicker} from "@ionic-native/date-picker";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

/**
 * Generated class for the FundTransferOtherBankHistory02Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-fund-transfer-other-bank-history02',
  templateUrl: 'fund-transfer-other-bank-history02.html',
})
export class FundTransferOtherBankHistory02Page {

  iTransferDetails;
  transId;
  transferType;
  iTransferOBHistoryError02;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public iTransferService: FundTransferOtherBankProvider,
              public datePicker: DatePicker,
              public gaProvider: GoogleAnalyticsProvider) {

    this.iTransferDetails = JSON.parse(navParams.get("iTransferDetails"));
  }

  showTransferDetails = function (listItem) {
    this.transId = listItem.appTransId.toString();
    this.transferType = listItem.transType.toString();

    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenComplete(data);
      });
  };

  onNewTokenGenComplete = function (data) {
    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encTransId = this.cryptoUtil.getEncryptVal(this.transId);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.iTransferService.getObFundTransferDetail(unMaskToken, appData.userId, appData.appId, encTransId)
        .then(data => {
          this.onTransferHistoryDetailsComplete(data);
        });
    }
  };

  onTransferHistoryDetailsComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('FundTransferOtherBankHistory03Page', {
        'iTransferDetails': JSON.stringify(data.object),
        'transferType': this.transferType
      });
    }

    else if (data.statusCode == 0) {
      this.iTransferOBHistoryError02 = data.note;
    }

    else {
      this.iTransferOBHistoryError02 = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {
  }

}



